import React, { useEffect, useState, Suspense } from "react";
import ReactGA from "react-ga";
import $ from "jquery";
import "./App.css";
import Header from "./Components/Header";
import ParticlesBg from "particles-bg";
import About from "./Components/About";

const ResumeComponent = React.lazy(() => import('./Components/Resume'));
const AboutComponent = React.lazy(() => import('./Components/About'));

function setCookie(name, value, days) {
  var expires = "";
  if (days) {
    var date = new Date();
    date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/";
}

function getCookie(name) {
  var nameEQ = name + "=";
  var ca = document.cookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == ' ') c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}

var randomID = function () {
  return '_' + Math.random().toString(36).substr(2, 9);
};

const getAnonymousId = () => {
  return getCookie('Sid_cookie') || '';
}

const setAnonymousId = id => {
  setCookie('Sid_cookie',id,365);
  window.user_id = id;
}

function initializeUser(){
  const cookieValue = getAnonymousId()
  if(!cookieValue){
    const id = randomID()
    return setAnonymousId(id);
  }
  window.user_id = cookieValue
}

export default function App(){
  const [resumeData , setResumeData] = useState({})

  initializeUser()
  ReactGA.initialize("UA-197285813-1" , { titleCase: false, gaOptions: { userId: window.user_id }});
  ReactGA.pageview(window.location.pathname + window.location.search);

  const getResumeData = () => {
    $.ajax({
      url: "./resumeData.json",
      dataType: "json",
      cache: false,
      success: function(data) {
        setResumeData(data)
      },
      error: function(xhr, status, err) {
        console.log(err);
        alert(err);
      }
    });
  }

  useEffect(() => {
    getResumeData();
  }, []);

  return (
    <div className="App">
      <ParticlesBg className="particlebg" type="cobweb" bg={true} color="white"/>
      <Header data={resumeData.main} />
      <Suspense fallback={<div>Bazingaa i am loading</div>}>
      <AboutComponent data={resumeData.main} />
      <ResumeComponent data={resumeData.resume} />
      </Suspense>
    </div>
  )
}