import React from "react";
import ParticlesBg from "particles-bg";
import Fade from "react-reveal";
import ReactGA from "react-ga";

export default function Header(props) {
  const sendEvent = data => {
    ReactGA.event({ category: "socialIcon", action: data });
  };

  
  if (!props.data) return null;

  const name = props.data.name;
  const description = props.data.description;

  const occupation = props.data.occupation;
  const city = props.data.address.city;

  const networks = props.data.social.map(function (network) {
    return (
      <li key={network.name}>
         <a onClick={()=> sendEvent(network.name)} class="tooltip" href={network.url} target="_blank" >
         <i className={network.className} />
          <span class="tooltiptext">{network.message}</span>
        </a>
      </li>
    );
  });

  return (
    <header id="home">
      {/* <ParticlesBg type="lines" bg={true} color="red" /> */}

      <nav id="nav-wrap">
        <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
          Show navigation
        </a>
        <a className="mobile-btn" href="#home" title="Hide navigation">
          Hide navigation
        </a>

        <ul id="nav" className="nav">
          <li className="current">
            <a
              rel="noreferrer"
              // target="_blank"
              className="smoothscroll"
              href="#home"
            >
              Home
            </a>
          </li>

          <li>
            <a
              rel="noreferrer"
              // target="_blank"
              className="smoothscroll"
              href="#about"
            >
              About
            </a>
          </li>

          <li>
            <a
              rel="noreferrer"
              // target="_blank"
              className="smoothscroll"
              href="#resume"
            >
              Resume
            </a>
          </li>
        </ul>
      </nav>

      <div className="row banner">
        <div className="banner-text">
          <h1 className="responsive-headline">I'm {name}.</h1>
          <h3>
            I'm a {city} based <span>{occupation}</span>. {description}.
          </h3>
          <hr />
          <ul className="social">{networks}</ul>
          <hr />
          <Fade bottom duration={2000}>
            <ul className="social">
              <a
                onClick={() => {
                  ReactGA.event({ category: "ButtonClick", action: "View resume" });
                  window.open("myresume.html", "_blank");
                }}
                rel="noreferrer"
                target="_blank"
                className="button btn project-btn smoothscroll"
              >
                <i className="fa fa-book"></i>View Resume
              </a>
              <a onClick={()=> ReactGA.event({ category: "ButtonClick", action: "Current work" }) }
               href="#resume" className="button btn project-btn smoothscroll">
                <i className="fa fa-bookmark-o"></i>Current work
              </a>
            </ul>
          </Fade>
        </div>
      </div>
      <p className="scrolldown">
        <a className="smoothscroll" href="#about">
          <i className="icon-down-circle"></i>
        </a>
      </p>
    </header>
  );
}
